/**
 * Build keys combinations.
 *
 * @param data key array
 * @returns keys
 * @example
 *    buildKeys(["example", "name"]) => ["EXAMPLE__NAME", "NAME"]
 */
const buildKeys = (...data: string[]) => {
  return data
    .filter((v) => (v?.length ?? 0) > 0)
    .map((v) => v.replace("-", "_").replace(" ", "_").toUpperCase())
    .map((_, i, a) => a.slice(i).join("."))
    .map((v) => v.replace(".", "__"))
}

export const findEnv = (
  data: string[],
  defaults?: string,
  env: Record<string, string | undefined> = process.env
): string | undefined => {
  const keys = buildKeys(...data)
  for (const key of keys) {
    const value = env[key]
    if (value !== undefined && value !== null) {
      return value
    }
  }

  return defaults
}
