import { toBool } from "@utils/converters"
import type { BaseContext, ContextPlugin } from ".."

import { parseInputs } from "@utils/inputs"
import { exec } from "@actions/exec"
import { info } from "@actions/core"

export class ExecContextPlugin implements ContextPlugin<"exec"> {
  readonly name = "exec"

  private dryrun: boolean = false
  init(ctx: BaseContext) {
    this.dryrun = parseInputs(ctx.name, "dryrun", "", toBool) ?? false
  }

  async run(cmd: string, ...args: string[]) {
    info(`$ ${cmd} '${args.join(" ")}'`)

    if (this.dryrun) return 0
    return await exec(cmd, args)
  }
}
